import React, { useRef, useState} from "react";
import * as CSS from "csstype";
import i18next from "../lang/i18n";
import { Property } from "@stitches/react/types/css";
import useEffectWhatChanged from "../hooks/useEffectWhatChanged";

type AdjustLabelProps = {
    style?: CSS.Properties<string | number>,
    maxFontSize?: CSS.Property.FontSize,
    minFontSize?: CSS.Property.FontSize,
    children?: React.ReactNode
};

const toPx = (fontSize: CSS.Property.FontSize | undefined) => {
    if (fontSize == undefined) return undefined;

    const fontSizeStr = fontSize.toString();
    let px: number | undefined = undefined;

    if (fontSizeStr.endsWith("rem")) {
        const rem = parseFloat(fontSizeStr.replace("rem", ""));
        px = rem * parseFloat(getComputedStyle(document.documentElement).fontSize);
    } else if (fontSizeStr.endsWith("px")) {
        const pxStr = fontSizeStr.replace("px", "");
        px = parseFloat(pxStr);
    }
    return px;
}

const getFillSize = (el: HTMLElement,
                     minFontSize: CSS.Property.FontSize | undefined,
                     maxFontSize: CSS.Property.FontSize | undefined) => {

    let fontSize = toPx(el.style.fontSize);
    console.debug(`AdjustLabel - getFillSize. init fontSize=${el.style.fontSize} -> ${fontSize}px`);

    const minFontSizePx = toPx(minFontSize) ?? Number.NEGATIVE_INFINITY;
    const maxFontSizePx = toPx(maxFontSize) ?? Number.POSITIVE_INFINITY;

    if (fontSize === undefined) {
        fontSize = Math.min(
            Math.max(Math.min(Number(el.offsetWidth) / 10, maxFontSizePx), minFontSizePx)
        );
    }

    console.debug(`AdjustLabel - getFillSize2. ${fontSize}px`);

    const step = 1;
    let complete;

    while (!complete) {
        el.style.fontSize = `${fontSize}px`;
        const overflowWidth = el.clientWidth < el.scrollWidth;

        console.debug(`AdjustLabel - getFillSize. ${overflowWidth} ${fontSize} / ${maxFontSizePx} / ${minFontSizePx}`);

        if (!overflowWidth) {
            complete = true;
        }

        if (overflowWidth && fontSize > step) {
            fontSize -= step;
        } else if (fontSize <= minFontSizePx) {
            fontSize = minFontSizePx;
            complete = true;
        }
    }

    el.style.fontSize = `${fontSize}px`;

    console.debug(`AdjustLabel - getFillSize. ret=${fontSize}`);

    return fontSize;
}

const AdjustLabel: React.FunctionComponent<AdjustLabelProps> = ({ style, children, maxFontSize, minFontSize }: AdjustLabelProps) => {
    const ref = useRef<HTMLDivElement>(null);
    const veryFirstFontSize = style?.fontSize;

    const [currentFont, setCurrentFont] = useState<Property.FontSize | undefined>(style?.fontSize);

    console.log(`AdjustLabel. veryFirstFontSize=${veryFirstFontSize}`)

    useEffectWhatChanged((mod: {}) => {
        if (ref.current != null) {
            if('language' in mod && veryFirstFontSize) {
                console.debug('AdjustLabel lang changed');
                ref.current.style.fontSize = veryFirstFontSize.toString();
            }

            setCurrentFont(getFillSize(ref.current, maxFontSize, minFontSize));
        }
    }, [currentFont, i18next.resolvedLanguage], [ 'currentFont', 'language']);

    return (
        <div
            ref={ref}
            style={{ ...style, whiteSpace: 'nowrap', fontSize: currentFont }}
        >
            { children }
        </div>
    );
};

export default AdjustLabel;