import {styled} from '@stitches/react';
import React, {MouseEvent, useEffect, useState} from 'react';
import { Button, message, Modal} from 'antd';
import {HeartFilled, LinkOutlined, MessageFilled} from '@ant-design/icons';
import CopyToClipboard from 'react-copy-to-clipboard';
import {CommonsType} from '../configs';
import {useTranslation} from "react-i18next";
import {COLORS, FONT_NAME, FONT_SIZE, getI18nFont, isEn} from "../constants";
import i18next from "../lang/i18n";

import CongratulatoryMoney from "./CongratulatoryMoney";
import * as CSS from "csstype";
import {ButtonProps} from "antd/es/button/button";

declare global {
  interface Window {
    Kakao: any;
  }
}

const isPortrait = window.matchMedia('(orientation: portrait)').matches;

// top, right, bottom, left
const ButtonGroup = styled('div', {
  width: 'min-content',
  display: 'flex',
  flexDirection: 'column',
  margin: 'auto',
  paddingBottom: isPortrait ? '10%' : '5%',
});

const ShareButton = (props: ButtonProps) => (
  <Button
      type='primary'
      icon={props.icon}
      id={props.id}
      size={props.size}
      onClick={props.onClick}
      style={{
        ...props.style,
        fontSize: FONT_SIZE.smallText,
        fontFamily: isEn ? FONT_NAME.LATO : FONT_NAME.MARU_BURI,
        height: isPortrait ? '2.5rem' : '5rem',
        minWidth: isPortrait ? '10rem' : '30rem',
      }}
  >
    { props.children }
  </Button>
);

const Description = styled('p', {
  fontSize: '1em',
  lineHeight: 1.75,
  opacity: 0.65,
  marginTop: '8px',
});


type ShareProps = {
  common: CommonsType;
};

const Share = ({ common }: ShareProps) => {
  const { t } = useTranslation();

  const [shareCount, setShareCount] = useState<number>(0);

  const [accountVisible, setAccountVisible] = useState<boolean>(false);

  if (!window.Kakao.isInitialized()) {
    window.Kakao.init(common.kakaoToken);
  }

  useEffect(() => {
    if (shareCount !== 0) {
      window.Kakao.Share.createDefaultButton({
        objectType: 'feed',
        container: '#sendKakao',
        content: {
          title: ` ${t('groom.shortName')} ❤${t('bride.shortName')} 결혼식에 초대합니다`,
          description: "아래의 '청첩장 열기' 버튼을 눌러주세요.🤵👰",
          imageUrl: window.location.origin + common.kakaoImagePath,
          link: {
            mobileWebUrl: common.url,
            webUrl: common.url,
          },
        },
        buttons: [
          {
            title: '청첩장 열기',
            link: {
              mobileWebUrl: common.url,
              webUrl: common.url,
            },
          },
        ],
        installTalk: true,
      });
      setTimeout(() => {
        document.getElementById('sendKakao')?.click();
        message.success('카카오톡으로 청첩장을 공유합니다!');
      }, 100);
    }
  }, [common, shareCount]);

  return (
    <>
      <ButtonGroup style={{ fontFamily: getI18nFont(FONT_NAME.LATO) }}>
        {i18next.resolvedLanguage === 'ko' ?
          <ShareButton
            style={{ margin: 8, textAlign: 'center' }}
            icon={<HeartFilled />}
            size="large"
            onClick={() => setAccountVisible(true)}
          >마음 전하실 곳</ShareButton> : <></>
        }

        {i18next.resolvedLanguage === 'ko' ?
          <ShareButton
            style={{ margin: 8, textAlign: 'center' }}
            icon={<MessageFilled />}
            id="sendKakao"
            size="large"
            onClick={() => setShareCount(shareCount + 1)}
          >
            카카오톡으로 공유하기
          </ShareButton> : <></>
        }

        <CopyToClipboard text={common.url}>
          <ShareButton
            style={{ margin: 8, textAlign: 'center' }}
            icon={<LinkOutlined />}
            size="large"
            onClick={() => message.success(t('linkCopied'))}
          >
            {t('copyLink')}
          </ShareButton>
        </CopyToClipboard>
      </ButtonGroup>
      <Modal
          title={<b>마음 전하실 곳(계좌번호)</b>}
          open={accountVisible}
          onOk={() => setAccountVisible(false)}
          onCancel={() => setAccountVisible(false)}
          cancelButtonProps={{ style: { display: 'none' } }}
          okButtonProps={{ style: { display: 'none' } }}
          footer={[<Description>계좌번호 클릭시 복사됩니다.</Description>]}
      >
        <CongratulatoryMoney />
      </Modal>
    </>
  );
};

export default Share;
