import { useEffect, useRef } from "react";

const usePrevious = (value: any, initialValue: any) => {
    const ref = useRef(initialValue);
    useEffect(() => {
        ref.current = value;
    });
    return ref.current;
};

const useEffectWhatChanged = (effectHook: ({}) => void, dependencies: any[], dependencyNames: string[] = []) => {
    const previousDeps = usePrevious(dependencies, []);

    const changedDeps = dependencies.reduce((accum, dependency, index) => {
        if (dependency !== previousDeps[index]) {
            const keyName = dependencyNames[index] || index;
            return {
                ...accum,
                [keyName]: {
                    before: previousDeps[index],
                    after: dependency
                }
            };
        }

        return accum;
    }, {});

    if (Object.keys(changedDeps).length) {
        console.debug('AdjustLabel changedDeps=' + JSON.stringify((changedDeps)));
    }

    useEffect(() => {
        effectHook(changedDeps);
    }, dependencies);
};

export default useEffectWhatChanged