import { useEffect, useRef, useState } from 'react';
import {styled} from '@stitches/react';
import PhotoAlbum, { Photo } from "react-photo-album";
import Zoom from "yet-another-react-lightbox/plugins/zoom";
import Lightbox from "yet-another-react-lightbox";
import "yet-another-react-lightbox/styles.css";

import useOnScreen from '../hooks/useOnScreen';

import { CommonsType } from '../configs';
import {COLORS, FONT_NAME, FONT_SIZE, getI18nFont} from "../constants";
import Title from "./Title";

const isPortrait = window.matchMedia('(orientation: portrait)').matches;

const Section = styled('section', {
    height: '100vh',
    overflow: 'hidden',
    transition: 'background 1s ease-in',
    display: 'flex',
    flexFlow: 'column',
});

// top, right, bottom, left

const GALLERY_WIDTH_BREAKPOINTS = [1600, 1400, 1200, 800, 0];
const GALLERY_PADDING = [20, 15, 10, 5, 0];
const index = GALLERY_WIDTH_BREAKPOINTS.findIndex((breakpoint) => {
    console.log(`window.innerWidth=${window.innerWidth}, breakpoint=${breakpoint}`);
    return breakpoint <= window.innerWidth;
});

const GalleryContainer = styled('div', {
    width: '100%',
    flex: '1 1 auto',
    flexFlow: 'column',
    minHeight: '0%',
    paddingLeft: `${GALLERY_PADDING[index]}%`,
    paddingRight: `${GALLERY_PADDING[index]}%`,
    paddingBottom: '3%',
});

type GalleryProps = {
    common: CommonsType;
};

const GallerySection = ({common}: GalleryProps) => {
    const ref = useRef<HTMLSelectElement>(null);
    const galleryContainerRef = useRef<HTMLDivElement>(null);

    const onScreen: boolean = useOnScreen<HTMLDivElement>(ref, '-125px');

    const [thumbImages, setThumbImages] = useState<Photo[]>([]);

    const [index, setIndex] = useState(-1);

    const getImgSize = (imgSrc: string, img: Photo) => {
        const newImg = new Image();
        newImg.onload = function (event) {
            if (this instanceof HTMLImageElement) {
                img.width = this.width;
                img.height = this.height;
            }
        }
        newImg.src = imgSrc; // this must be done AFTER setting onload
    };

    useEffect(() => {
        const customImages = common.galleryThumbImages.map((image, index) => {
            const imgObj = {
                key: `${index}`,
                src: image,
                width: 10,
                height: 10,
            }
            getImgSize(image, imgObj);
            return imgObj;
        });

        setThumbImages(customImages);
    }, [common]);

    const slides = common.galleryImages.map((image, index) => ({
        src: image,
        key: `${index}`,
    }));

    /*
    const isOverflown = (element: HTMLElement | null) => {
        if(element == null) return false;
        return element.scrollHeight > element.clientHeight;
    }
    */

    return (
        <Section
            ref={ref}
            style={{
                background: onScreen ? COLORS.pine : COLORS.eucalyptus,
                transition: 'background 1s ease-in',
            }}
        >
            <Title style={{ color: COLORS.gold,  textShadow: '1px 1px 2px rgb(190 155 105)' }}>Gallery</Title>
            <GalleryContainer ref={galleryContainerRef}>
                <PhotoAlbum
                    layout="rows"
                    photos={thumbImages}
                    onClick={(event, photo, index) => setIndex(index)}
                    spacing={(containerWidth) => {
                        if (containerWidth >= 1200) return 10;
                        else if (containerWidth >= 600) return 10;
                        else if (containerWidth >= 300) return 4;
                        return 2;
                    }}
                />
                <Lightbox
                    slides={slides}
                    open={index >= 0}
                    index={index}
                    plugins={[Zoom]}
                    close={() => setIndex(-1)}
                />
            </GalleryContainer>
        </Section>
    );
};

export default GallerySection;
