import {styled} from "@stitches/react";
import * as CSS from "csstype";
import React from "react";

// top, right, bottom, left
// top, horizontal, bottom
// vertical, horizontal
const ImageLayout = styled('div', {
    /* flex-grow, flex-shrink, flex-basis */
    flex: '1 1 auto',
    display: 'flex',
    flexFlow: 'column',
    minHeight: '0%',
    animation: 'fadein 1s',
    padding: '0rem 1.5rem',
});

const Image = styled('img', {
    objectFit: 'scale-down',
    overflow: 'hidden',

    // parent flexFlow: 'column' & flexGrow: '0' -> top
    // parent flexFlow: 'column' & flexGrow: '1' -> middle
    flexGrow: '1',
    maxHeight: '100%'
});

type ScaleImageProps = {
    style?: CSS.Properties<string | number>,
    src?: string,
    alt?: string,
};

const ScaleImage = ({ style, src, alt }: ScaleImageProps) => {

    return (
        <ImageLayout style={{ ...style }} >
            <Image src={ src } alt={ alt } />
        </ImageLayout>
    );
};

export default ScaleImage;

