import {styled} from "@stitches/react";
import {COLORS, FONT_NAME, FONT_SIZE, getI18nFont, isEn} from "../constants";
import * as CSS from "csstype";
import React, {useEffect} from "react";
import useEffectWhatChanged from "../hooks/useEffectWhatChanged";
import i18next from "../lang/i18n";
import AdjustLabel from "./AdjustLabel";

const isPortrait = window.matchMedia('(orientation: portrait)').matches;

// top, right, bottom, left
// top, horizontal, bottom
// vertical, horizontal
const StyledTitle = styled('div', {
    color: COLORS.gold,
    fontFamily: isEn ? FONT_NAME.ALEX_BRUSH : FONT_NAME.MARU_BURI,
    textAlign: 'center',
    width: '100%',
    fontSize: FONT_SIZE.titleText3,
    textShadow: '1px 1px 2px rgb(203 186 162)',
    margin: 0,
    fontWeight: '500',
    whiteSpace: 'nowrap',
    padding: isPortrait ? '15% 5% 10% 5%' : '5% 10% 5% 10%',
});

type TitleProps = {
    style?: CSS.Properties<string | number>,
    children?: React.ReactNode
};

const Title = ({ style, children }: TitleProps) => {

    // useEffect(() => {
    //     console.log('lang changed')
    // }, [i18next.resolvedLanguage]);

    return (
        <StyledTitle style={{ ...style }} ><AdjustLabel style={{ fontSize: FONT_SIZE.titleText3 }}>{ children }</AdjustLabel></StyledTitle>
    );
}

export default Title;
